.blog-grid-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: no-repeat center center;
  background-size: cover;
  padding: 0 15px;
  /* size */
  /* color */
  /* content */
}

.blog-grid-card_small {
  height: 420px;
}

.blog-grid-card_big {
  height: 840px;
  align-items: flex-end;
  padding-bottom: 50px;
}

@media (max-width: 575px) {
  .blog-grid-card_big {
    height: 420px;
  }
}

.blog-grid-card_white .blog-grid-card__before {
  background-color: rgba(255, 255, 255, 0.9);
}

.blog-grid-card_white .blog-grid-card__avatar:before, .blog-grid-card_white .blog-grid-card__avatar:after {
  background-color: rgba(0, 0, 0, 0.03);
}

.blog-grid-card_white .blog-grid-card__desc {
  color: rgba(43, 44, 45, 0.5);
}

.blog-grid-card_white .blog-grid-card__name:hover,
.blog-grid-card_white .blog-grid-card__title:hover {
  color: #000000;
}

.blog-grid-card_dark .blog-grid-card__before {
  background-color: rgba(0, 0, 0, 0.7);
}

.blog-grid-card_dark .blog-grid-card__avatar:before, .blog-grid-card_dark .blog-grid-card__avatar:after {
  background-color: rgba(255, 255, 255, 0.05);
}

.blog-grid-card_dark .blog-grid-card__name,
.blog-grid-card_dark .blog-grid-card__title,
.blog-grid-card_dark .blog-grid-card__time {
  color: #ffffff;
}

.blog-grid-card_dark .blog-grid-card__desc {
  color: rgba(255, 255, 255, 0.5);
}

.blog-grid-card_colored .blog-grid-card__before {
  background-color: rgba(var(--brand-primary-rgb), 0.9);
}

.blog-grid-card_colored .blog-grid-card__avatar:before, .blog-grid-card_colored .blog-grid-card__avatar:after {
  background-color: rgba(255, 255, 255, 0.05);
}

.blog-grid-card_colored .blog-grid-card__name,
.blog-grid-card_colored .blog-grid-card__title,
.blog-grid-card_colored .blog-grid-card__time {
  color: #ffffff;
}

.blog-grid-card_colored .blog-grid-card__desc {
  color: rgba(255, 255, 255, 0.5);
}

.blog-grid-card_colored .blog-grid-card__cat {
  background-color: #2b2c2d;
}

.blog-grid-card:hover .blog-grid-card__avatar:before {
  animation: blog-grid-pulse 1.5s infinite;
  animation-delay: 0.3s;
}

.blog-grid-card:hover .blog-grid-card__avatar:after {
  animation: blog-grid-pulse 1.5s infinite;
}

.blog-grid-card__before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.blog-grid-card__item {
  position: relative;
  z-index: 3;
  max-width: 534px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.blog-grid-card__avatar {
  width: 102px;
  height: 102px;
  border-radius: 50%;
  position: relative;
  display: block;
  margin: 0 auto 25px auto;
}

.blog-grid-card__avatar:before, .blog-grid-card__avatar:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 50%;
  pointer-events: none;
}

.blog-grid-card__avatar:before {
  transform: scale(1.14);
}

.blog-grid-card__avatar:after {
  transform: scale(1.28);
}

.blog-grid-card__avatar img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: auto;
  border-radius: 50%;
  z-index: 3;
}

.blog-grid-card__name {
  margin-bottom: 35px;
}

@media (max-width: 575px) {
  .blog-grid-card__name {
    margin-bottom: 25px;
  }
}

.blog-grid-card__title {
  margin-bottom: 15px;
  font-size: 1.5rem;
  line-height: 28px;
}

@media (max-width: 575px) {
  .blog-grid-card__title {
    font-size: 1.125rem;
    line-height: 22px;
  }
}

.blog-grid-card__desc {
  margin-bottom: 18px;
  font-size: 1.125rem;
  line-height: 24px;
}

@media (max-width: 575px) {
  .blog-grid-card__desc {
    font-size: 1rem;
    line-height: 22px;
  }
}

.blog-grid-card__cat {
  padding: 0 12px;
  color: #ffffff;
  border-radius: 12px;
  margin: 0 7px;
  background-color: var(--brand-primary);
}

.blog-grid-card__cat:hover {
  color: #ffffff;
}

.blog-grid-card__time {
  margin: 0 7px;
}

.blog-grid-card__time i {
  line-height: inherit;
  margin-right: 2px;
}

@keyframes blog-grid-pulse {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.8);
  }
  100% {
    transform: scale(1.1);
    opacity: 0;
  }
}
